import React, { useEffect, useState } from 'react';
import './waybills.css';
import { useParams } from 'react-router';
import { Typography } from '@material-ui/core';
import { httpFetch } from '../../services/http-fetch';
import { dateToLocalTime, onlyLocalDate } from '../../utils/date';
import checkupResultReplacer from '../../utils/CheckupResultReplacer';
import techInspectionReplacer from '../../utils/techInspectionRaplacer';
import { baseUrl } from '../../services/base-url';
import { useHistory } from 'react-router-dom';
import { FetchData } from '../../gateways/http-fetch/http-fetch-response';
import NavigationHeader from '../../view/components/common/navigation-header/navigation-header';
import Card from '../../view/components/common/card/card';
import Delimiter from '../../view/components/common/delimiter/delimiter';

type WaybillsId = {
  id: string;
};

const Waybills = () => {
  const { id }: WaybillsId = useParams();
  const [waybill, setWaybill] = useState<FetchData>({ attributes: {} });
  const history = useHistory();

  useEffect(() => {
    httpFetch
      .get(
        `${baseUrl}/waybills/${id}`,
        {},
        {
          'Content-Type': 'multipart/form-data',
        },
      )
      .then(async data => {
        if (data) {
          await setWaybill(data.data);
        } else {
          history.push('/waybillslist');
        }
      });
  }, []);
  return (
    <div className="waybill">
      {waybill && Object.keys(waybill.attributes).length !== 0 && (
        <>
          <NavigationHeader>
            Лист <b>№{id}</b>
          </NavigationHeader>

          <div className="waybill-date-wrapper">
            <Typography className="waybill-date-label">Дата выдачи</Typography>
            <Typography className="waybill-date-date">
              {waybill.attributes.datetime && onlyLocalDate(waybill.attributes.datetime)}
            </Typography>
          </div>
          <Delimiter />

          <Typography className="waybill-title">Предрейсовые осмотры</Typography>

          <Card color="white" label="Тип осмотра" className="field__med">
            {waybill.attributes.type === 0 ? 'Предрейсовый' : 'Послерейсовый'}
          </Card>

          <Card color="white" label="Дата / время мед. осмотра" className="field__med">
            {waybill.attributes.checkup && dateToLocalTime(waybill.attributes.checkup.datetime_end)}
          </Card>

          <Card color="white" label="ФИО врача" className="field__med">
            {waybill.attributes.checkup &&
              waybill.attributes.checkup.medic &&
              `${waybill.attributes.checkup.medic.last_name} ${waybill.attributes.checkup.medic.first_name[0]}. ${waybill.attributes.checkup.medic.middle_name[0]}.`}
          </Card>

          <Card color="white" label="Статус допуска мед. осмотра" className="field__med">
            {waybill.attributes.checkup &&
              checkupResultReplacer(waybill.attributes.checkup.result_medic)}
          </Card>

          <Card color="white" label="Дата / время тех. осмотра" className="field__med">
            {waybill.attributes.tech_inspection &&
              dateToLocalTime(waybill.attributes.tech_inspection.date)}
          </Card>

          <Card color="white" label="ФИО механика" className="field__med">
            {waybill.attributes.tech_inspection &&
              waybill.attributes.tech_inspection.mechanic &&
              `${waybill.attributes.tech_inspection.mechanic.last_name} ${waybill.attributes.tech_inspection.mechanic.first_name[0]}. ${waybill.attributes.tech_inspection.mechanic.middle_name}.`}
          </Card>

          <Card color="white" label="Статус допуска тех осмотра" className="field__med">
            {waybill.attributes.tech_inspection &&
              techInspectionReplacer(waybill.attributes.tech_inspection.result)}
          </Card>

          <Card color="white" label="Пункт осмотра" className="field__med">
            {waybill.attributes.inspection_point.name}
          </Card>

          <Card color="white" label="Пробег" className="field__med">
            {waybill.attributes.mileage}
          </Card>

          <Card color="white" label="Фото водителя" className="field__med">
            <img
              className="waybill-photo"
              src={waybill.attributes.driver_photo}
              alt="driver_photo"
            />
          </Card>

          {waybill.attributes.vehicle_photo.length > 0 && (
            <>
              <Typography className="waybill-date-label">Фото автомобиля</Typography>
              {waybill.attributes.vehicle_photo.map((item: string, index: number) => {
                return (
                  <Card color="white" key={index} className="field__med">
                    <img className="waybill-photo" src={item} alt="photo" />
                  </Card>
                );
              })}
            </>
          )}
        </>
      )}
    </div>
  );
};

export default Waybills;
